"use client"
import React from 'react'
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { ToastContainer, toast } from 'react-toastify';
import clientPromise from '@/lib/mongodb';
import { useEffect } from 'react';
import { useSession } from "next-auth/react";
import { useSearchParams } from 'next/navigation';

const generate = () => {
    const { data: session } = useSession();
    const usehParams = useSearchParams()
    const router = useRouter()
    const [username, setusername] = useState(`${session?.user?.name}`)
    const [bio, setbio] = useState("")
    const [addlinks, setaddlinks] = useState([{link:"", text:""}])
    const [handel, sethandel] = useState(usehParams.get('handel'))
    const [ppic, setppic] = useState("")

    const handleChange = (index, link, text) => { 
        setaddlinks((initialLinks)=>{
          return initialLinks.map((item, i)=>{
            if (i==index){
              return {link, text}
            }
            else {
              return item
            }
          })
        })
       }


       useEffect(() => {
        
       }, [])
       
    

   const addLinks = () => {
     setaddlinks(addlinks.concat([{text:"", link:""}]))
   }
   
    
const submitLinks = async () => {
    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    
    const raw = JSON.stringify({
      "links": addlinks,
      "username": username,
      "handel": handel,
      "profilepic":ppic,
      "bio": bio
    });
    console.log(raw)
    
    const requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
      redirect: "follow"
    };
    
    const r = await fetch("http://localhost:3000/api/add", requestOptions);
    const result = await r.json();
    if(result.success === "true"){ 
      toast.success(result.message)
      setaddlinks([{link:"", text:""}])
      sethandel("")
      setppic("")
     }
     else{
       toast.error(result.message)
     }
    router.push(`/${username}`)
}


  return (
   <>
    <section className='bg-purple-300 min-h-[100vh] py-6'>
      <div className='h-[650px] w-full md:w-3xl mx-auto px-4'>
       <h1 className='text-center text-black py-7 text-4xl font-bold'>Edit your profile</h1>
      <div className='h-[500px] custom-scrollbar overflow-auto'>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">step 1 : Username</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={username || ""} onChange={(e) => setusername(e.target.value)} type="text" placeholder='Enter your name' />
       </div>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">step 2 : Name</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={handel || ""} onChange={(e) => sethandel(e.target.value)} type="text" placeholder='Enter your name' />
       </div>

        <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">step 3 : Add links</label>
       {addlinks && addlinks.map((item, index) =>{
        return  <div className='flex gap-1.5 mb-4' key={index}>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={item.text || ""} onChange={e => {handleChange(index,  item.link, e.target.value)}} type="text" placeholder='Site Name' />
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={item.link || ""} onChange={e => {handleChange(index, e.target.value, item.text)}} type="text" placeholder='Site link' />
        </div>
       })}
        <button onClick={()=>addLinks()} className="bg-gray-800 hover:bg-gray-700 py-2 px-7 mx-auto cursor-pointer text-white rounded-2xl">+ Add more Links</button>
       </div>

       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">step 4 : Profile picture</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={ppic || ""} onChange={(e) => setppic(e.target.value)} type="text" placeholder='Enter your pofile pic' />
       </div>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">step 5 : Bio</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' value={bio || ""} onChange={(e) => setbio(e.target.value)} type="text" placeholder='Enter your pofile pic' />
       </div>
       </div>
      </div>
       <div className='w-full flex justify-center '>
       <button disabled={ppic == "" || handel == "" || addlinks[0].text == ""} className="bg-gray-800 disabled:bg-slate-700 hover:bg-gray-700 fixed bottom-10 py-2 px-7 text-lg mx-auto cursor-pointer text-white rounded-2xl" onClick={()=>submitLinks()}>Save profile</button>
       </div>
      <ToastContainer />
    </section>
   </>
  )
}

export default generate
